<template>
  <el-form
    ref="form"
    v-loading="loading"
    :model="model"
    :label-position="mainConfig.labelPosition"
    :label-width="`${mainConfig.labelWidth}px`"
    :rules="rulesConfig"
    :inline="mainConfig.layout === 'inline'"
    :class="formClass"
  >
    <!-- layout inline -->
    <template v-if="mainConfig.layout === 'inline'">
      <el-form-item
        v-for="config in fieldListConfig"
        :key="config.name"
        :label="config.label"
        :prop="config.name"
        v-show="config.show"
        @click.native="$emit('click-form-item', config)"
      >
        <component :is="`gc-${config.type}`" v-model="model[config.name]" v-bind="config" @changeDept="changeDept" />
      </el-form-item>

      <el-form-item v-for="(btn, i) in operateConfig" :key="i">
        <el-button v-if="btn.type === 'submit'" @click="submit(btn.action, btn.callback)"></el-button>
        <el-button class="line" v-else-if="btn.type === 'search'" :key="i" @click="btn.action"></el-button>
        <el-button v-else :key="i" @click="btn.action">{{ btn.label }}</el-button>
      </el-form-item>

      <el-form-item>
        <slot name="btns"></slot>
      </el-form-item>
    </template>

    <!-- layout grid -->

    <!-- layout row-col -->
    <template v-else>
      <el-row v-for="(row, rowIndex) in fieldListConfig" :key="rowIndex">
        <!-- <el-divider content-position="left"></el-divider> -->
        <!-- TODO config.labelWidthmainConfig.labelWidth -->
        <el-col v-for="config in row" :key="config.name" :span="config.span">
          <el-form-item
            :label="config.label"
            :prop="config.name"
            v-show="config.show"
            :label-width="`${config.labelWidth || mainConfig.labelWidth}px`"
            @click.native="$emit('click-form-item', config)"
          >
            <component :is="`gc-${config.type}`" v-model="model[config.name]" v-bind="config" @changeDept="changeDept" />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row v-if="operateConfig && operateConfig.length > 0">
        <el-form-item style="float: right">
          <template v-for="(btn, i) in operateConfig">
            <el-button v-if="btn.type === 'submit'" :key="i" @click="submit(btn.action, btn.callback)"></el-button>
            <el-button v-else-if="btn.type === 'submitForm'" :key="i" @click="submitForm(btn.action, btn.callback)"></el-button>
            <el-button v-else-if="btn.type === 'search'" :key="i" @click="btn.action"></el-button>
            <el-button v-else :key="i" @click="btn.action">{{ btn.label }}</el-button>
          </template>
          <slot name="btns"></slot>
        </el-form-item>
      </el-row>
    </template>
  </el-form>
</template>

<script>
/**
 * TODO labelWidth 
 * TODO  v-permission  
 * TODO  clear  
 * TODO form-item
 *
 * TODO select-api  
 *
 * TODO 
 * TODO 
 * TODO  
 * TODO 
 * TODO 
 *
 * FIXME
 */

import { formConfigValidator } from './tools';

export default {
  name: 'gc-form',
  props: {
    model: { type: Object, required: true, default: () => ({}) }, // 
    formConfig: { required: true, validator: formConfigValidator }, // 
  },
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    // 
    mainConfig() {
      const config = Object.assign({}, this.formConfig.mainConfig);
      config.labelWidth = config.layout === 'inline' ? '' : config.labelWidth;
      return config;
    },
    // 
    fieldListConfig() {
      // span tagName
      const formConfigComplete = this.formConfig.fieldList.map((x) => {
        const item = Object.assign({}, x);
        // span
        if (item.span === null || item.span === undefined) {
          item.span = this.formConfig.mainConfig.span;
        }
        item.span = parseInt(item.span, 10);
        // show
        item.show = item.show === false ? false : true;
        return item;
      });
      //  inline
      if (this.formConfig.mainConfig.layout === 'inline') {
        return formConfigComplete;
      }

      //  col-row span
      const result = [[]];
      formConfigComplete.forEach((x) => {
        const reduceIndex = result[result.length - 1].reduce((ac, cu) => ac + (cu.show ? cu.span : 0), 0) + (x.show ? x.span : 0); // show: false 
        if (reduceIndex > 24) {
          result.push([x]);
        } else {
          result[result.length - 1].push(x);
        }
      });
      return result;
    },
    // 
    operateConfig() {
      return this.formConfig.operateConfig || [];
    },
    // 
    rulesConfig() {
      const result = {};
      this.formConfig.fieldList.forEach((x) => {
        if (Array.isArray(x.rules)) {
          result[x.name] = x.rules;
        }
      });
      return result;
    },
    // 
    formClass() {
      return { sBar: this.mainConfig.layout === 'inline', bg: this.mainConfig.layout === 'inline', font_medium: true };
    },
  },
  methods: {
    //->
    changeDept(deptCode, deptName) {
      this.$emit('changeDept', deptCode, deptName);
    },
    /**
     * 
     *
     * TODO submit    
     */
    submit(api, callback) {
      if (typeof api !== 'function') {
        console.error('');
        return false;
      }
      // TODO loading
      this.loading = true;
      this.$refs.form.validate((valid) => {
        if (!valid) {
          this.$emit('error', valid);
          this.loading = false;
          return false;
        }
        api(this.model)
          .then((res) => {
            if (res.success === true) {
              this.$message.success(res.content);
              this.$emit('success', res);
            } else {
              this.$message.error(res.content);
              this.$emit('error', res);
            }
          })
          .catch((err) => {
            this.$message.error(err.content);
            this.$emit('error', err);
          })
          .finally(() => {
            this.loading = false;
            typeof callback === 'function' && callback();
          });
      });
    },
    submitForm(api, callback) {
      if (typeof api !== 'function') {
        console.error('');
        return false;
      }
      // TODO loading
      this.loading = true;
      this.$refs.form.validate((valid) => {
        if (!valid) {
          this.$emit('error', valid);
          this.loading = false;
          return false;
        }
        const fd = new FormData();
        for (const key in this.model) {
          const value = this.model[key];
          if (value === null || value === undefined) {
            continue;
          }
          // 
          const typeItem = this.formConfig.fieldList.find((x) => x.name === key);
          if (typeItem && typeItem.type === 'pic') {
            const file = this.model[key];
            // fd.append(key, '');
            file instanceof File && fd.append(typeItem.filename, file);
          } else {
            fd.append(key, this.model[key]);
          }
        }
        api(fd)
          .then((res) => {
            if (res.success === true) {
              this.$message.success(res.content);
              this.$emit('success', res);
            } else {
              this.$message.error(res.content);
              this.$emit('error', res);
            }
          })
          .catch((err) => {
            this.$message.error(err.content);
            this.$emit('error', err);
          })
          .finally(() => {
            this.loading = false;
            typeof callback === 'function' && callback();
          });
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.el-input-group__append > .file-operate:not(:first-child) {
  border-left: solid 1px #b9bec6;
  margin-left: 10px;
  padding-left: 10px;
}
</style>
